/* globales */
label{
    padding: 0 5px;
    font-size: 25px;
}

span{
    font-size: 20px;
}

input.danger{
    border: 2px solid red;
}

.errors{
    font-size: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}
.errors p{
    padding: 3px
}


/* bodyForm, contenedor superior*/
.bodyForm{
    font-family: 'Amatic SC', cursive;
    padding: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.bodyForm h3{
    font-size: 40px;
}

.bodyForm button{
    font-size: 20px;
    font-family: 'Amatic SC', cursive;
    margin: 0 2px;
    padding: 5px 5px;
    box-shadow: transparent;
    border-radius: 20px;
    border: 1px solid #d4c4b5;
    background-color: #d4c4b5;

}

.bodyForm button:hover{
    font-size: 25px;
    height: 50px;
    width: 85px;
    border-radius: 50px;
    background-color: white;
}

.bodyForm button:disabled{
    background-color: #d4c4b5 ;
}

/*createForm, card */
.createForm{
    background-color: #fef8d5;
    padding: 30px 10px;
    margin: 50px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    width: 400px;
    min-height: 300px;
    border-radius: 15px;
 
}
 /*divCreateForm interno*/
.divCreateForm{
    padding: 5px;    
}

.divCreateForm input{
    padding: 0 5px;
    width: 20px;
}


 /*divCreateSelect interno*/
.divCreateSelect{
    padding: 5px;
}

 /*divCreateName interno*/
.divCreateName{
    padding: 5px;  
}


.divCreateName span{
    padding: 3px;
    font-size: 18px;
}

.divCreateName button{
    font-family: 'Amatic SC', cursive;
    text-align: center;
    font-size: 12px;
    padding: 0;
    width: 18px;
    height: 18px;
    border-radius: 10px;
 
}
.divCreateName button:hover{
    width: 30px;
    height: 30px;
    
}

/*newBreedBttn, hace display una vez agregado el perrito*/
#newBreedBttn{
    font-size: 20px;
    height: 40px;
    width: 120px;

}
#newBreedBttn:hover{
    font-size: 25px;
    height: 50px;
    width: 140px;
}

/*title, principio de página*/

.title{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: max-content;
}
.title h3{
    margin: 0 10px
}