@import url(https://fonts.googleapis.com/css2?family=WindSong:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=WindSong:wght@500&display=swap);
body {
  display: block;
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html{
  display: block;
  height: 100%; 
  background-color:#ffde15 ;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.App {
  
  text-align: center;
  display: block;
  height: 100%; 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
#root{
  font-family: 'Amatic SC', cursive;
  display: block;
  height: 100%; 
  background-color: #ffde15;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.detailPage{
    font-family: 'Amatic SC', cursive;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 150px;
}
.containerDetail button{
    font-family: 'Amatic SC', cursive;
    cursor: pointer;
    font-size: 15px;
    text-align: center;
    margin: 5px;
    background: #d4c4b5;
    color: #000;
    height: 30px;
    width: 70px;
    box-shadow: transparent;
    border-radius: 15px;
    border: 1px solid #d4c4b5;

}
.containerDetail button:hover{
    font-size: 18px;
    height: 35px;
    width: 80px;
    border-radius: 60px;
    background-color: white;
}
.containerDetail{
    border: 1px solid #7D732F;;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #fff7d5;
    border-radius: 15px;
    padding: 15px 10px;
    height: 400px;
    margin: 10px 0;
    width: 400px;
}
.containerDetail h1{
    font-size: 30px;
}
.containerDetail img{
    margin: 5px 0;
    border-radius: 10px;
    width: auto;
    max-width: 90%;
    max-height: 125px;
}
.containerDetail h3{
    padding: 3px 0;
    font-size: 20px;
}

/* globales */
label{
    padding: 0 5px;
    font-size: 25px;
}

span{
    font-size: 20px;
}

input.danger{
    border: 2px solid red;
}

.errors{
    font-size: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}
.errors p{
    padding: 3px
}


/* bodyForm, contenedor superior*/
.bodyForm{
    font-family: 'Amatic SC', cursive;
    padding: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.bodyForm h3{
    font-size: 40px;
}

.bodyForm button{
    font-size: 20px;
    font-family: 'Amatic SC', cursive;
    margin: 0 2px;
    padding: 5px 5px;
    box-shadow: transparent;
    border-radius: 20px;
    border: 1px solid #d4c4b5;
    background-color: #d4c4b5;

}

.bodyForm button:hover{
    font-size: 25px;
    height: 50px;
    width: 85px;
    border-radius: 50px;
    background-color: white;
}

.bodyForm button:disabled{
    background-color: #d4c4b5 ;
}

/*createForm, card */
.createForm{
    background-color: #fef8d5;
    padding: 30px 10px;
    margin: 50px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    width: 400px;
    min-height: 300px;
    border-radius: 15px;
 
}
 /*divCreateForm interno*/
.divCreateForm{
    padding: 5px;    
}

.divCreateForm input{
    padding: 0 5px;
    width: 20px;
}


 /*divCreateSelect interno*/
.divCreateSelect{
    padding: 5px;
}

 /*divCreateName interno*/
.divCreateName{
    padding: 5px;  
}


.divCreateName span{
    padding: 3px;
    font-size: 18px;
}

.divCreateName button{
    font-family: 'Amatic SC', cursive;
    text-align: center;
